import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic } from "../../../actions";
import {
  FC_GetAllTransfers,
  FT_GetAllTransfer,
} from "../../../actions/transfer.action";
import Button from "../../../components/Buttons/Button";
import DownloadLink from "../../../components/DownloadLink/DownloadLink";
import TextInput from "../../../components/Inputs/TextInput";
import TableLoading from "../../../components/TableLoading/TableLoading";
import { StoreState } from "../../../reducers";
import { TransferReducerType } from "../../../reducers/transfer.reducer";
import { TransferRequestType } from "../../../types/TransferTypes";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
  search,
} from "../../../utils/functions";
import LeaveForm from "./LeaveForm";
import TransferForm from "./TransferForm";

type TransferRequestsProps = {
  auth: Auth;
  basic: Basic;
  transfer: TransferReducerType;
  FC_GetAllTransfers: FT_GetAllTransfer;
};

type TransferRequestsState = {
  loading: boolean;
  error: {
    target: "main-error" | "main-success";
    msg: string;
  } | null;
  search: string;
  selectedUserId: string;
};

class TransferRequests extends Component<
  TransferRequestsProps,
  TransferRequestsState
> {
  constructor(props: TransferRequestsProps) {
    super(props);

    this.state = {
      loading: true,
      error: null,
      search: "",
      selectedUserId: "",
    };
  }

  componentDidMount = () => {
    if (
      this.props.transfer.allTransfers === null ||
      this.props.transfer.allTransfers.length === 0
    )
      this.props.FC_GetAllTransfers(
        (status: boolean, data: TransferRequestType[] | null, msg: string) => {
          this.setState({ loading: status });

          if (!status && data) this.setState({ loading: status });
          else if (!status && !isEmptyOrSpaces(msg))
            this.setState({
              error: { target: "main-error", msg: msg },
            });
        }
      );
    else if (this.state.loading === true) this.setState({ loading: false });
  };

  getTransferById = (transfer_id: string): TransferRequestType | undefined => {
    if (this.props.transfer.allTransfers)
      return this.props.transfer.allTransfers.find(
        (item) => item.teacher_transfer_id.toString() === transfer_id
      );
    return undefined;
  };
  render() {
    return (
      <>
        <div className="mt-3">
          <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
            <div className="flex-1 pr-2">
              <TextInput
                error=""
                label="Search"
                type="search"
                name=""
                onChange={(e) => this.setState({ search: e.target.value })}
                placeholder="Search"
                value={this.state.search}
                size="sm"
              />
            </div>
          </div>
          <div className="w-full overflow-x-auto mt-4">
            <table className="w-full">
              <thead className="bg-blue-50 text-blue-800 font-bold text-base text-left">
                <tr>
                  <th className="p-2">#</th>
                  <th className="p-2">Full Name</th>
                  <th className="p-2">Phone</th>
                  <th className="p-2">Current School</th>
                  <th className="p-2">Approved School</th>
                  <th className="p-2">Approved Sector</th>
                  <th className="p-2" colSpan={2}>
                    Status
                  </th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading ? (
                  <TableLoading cols={8} />
                ) : this.props.transfer.allTransfers === null ? (
                  <tr className="border-t border-b">
                    <td
                      colSpan={8}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      Something went wrong! Please try again.
                    </td>
                  </tr>
                ) : this.props.transfer.allTransfers.length <= 0 ? (
                  <tr>
                    <td
                      colSpan={8}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      No results found
                    </td>
                  </tr>
                ) : (
                  search(
                    this.props.transfer.allTransfers,
                    this.state.search
                  ).map((element: TransferRequestType, index: number) => {
                    return (
                      <tr
                        key={element.teacher_transfer_id}
                        className="border-t border-b hover:bg-gray-50"
                      >
                        <td className="p-2">{index + 1}</td>
                        <td className="p-2">{element.teacher_full_name}</td>
                        <td className="p-2">{element.teacher_phone_number}</td>
                        <td className="p-2">
                          {element.school_from_info
                            ? element.school_from_info.school_name
                            : ""}
                        </td>
                        <td className="p-2">
                          {element.requested_school_info
                            ? element.requested_school_info.school_name
                            : ""}
                        </td>
                        <td className="p-2">
                          {element.requested_school_info
                            ? element.requested_school_info.sector_name
                            : ""}
                        </td>
                        {this.state.selectedUserId ===
                        element.teacher_transfer_id.toString() ? (
                          <td className="">
                            <LeaveForm
                              selectedUserId={this.state.selectedUserId}
                              setSelectedUserId={(value: string) =>
                                this.setState({ selectedUserId: value })
                              }
                            />
                          </td>
                        ) : (
                          <>
                            <td className="p-2">
                              {element.mayor_letter === null ||
                              isEmptyOrSpaces(element.mayor_letter) ? (
                                <Button
                                  theme="accent-light"
                                  onClick={(e) =>
                                    this.setState({
                                      selectedUserId:
                                        element.teacher_transfer_id.toString(),
                                    })
                                  }
                                >
                                  Upload signed letter
                                </Button>
                              ) : (
                                element.requested_status
                              )}
                            </td>
                            <td className="p-2">
                              {element.mayor_letter === null ||
                              isEmptyOrSpaces(element.mayor_letter) ? (
                                <DownloadLink
                                  linkFor="transfer"
                                  first_id={element.teacher_transfer_id}
                                  last_id={element.teacher_id}
                                  title={"Transfer letter"}
                                />
                              ) : (
                                element.mayor_letter &&
                                !isEmptyOrSpaces(element.mayor_letter) && (
                                  <DownloadLink
                                    linkFor="uploaded"
                                    first_id={element.mayor_letter}
                                    last_id={""}
                                    title={"Transfer letter"}
                                  />
                                )
                              )}
                            </td>
                          </>
                        )}
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  transfer,
}: StoreState): { auth: Auth; basic: Basic; transfer: TransferReducerType } => {
  return { auth: auth, basic: basic, transfer };
};

export default connect(mapStateToProps, {
  FC_GetAllTransfers,
})(TransferRequests);
